import WebApi from "api/WebApi";
export function CreateChannel(payload) {
    return WebApi.Post("channels", payload);
}
export function UpdateChannel(payload) {
    return WebApi.Put(`channels/${payload.id}`, payload);
}
export function GetChannel(id) {
    return WebApi.Get(`channels/${id}`, {});
}
export function GetChannelBySlug(slug) {
    return WebApi.Get(`channels/${slug}`, {});
}
export function GetAllChannels() {
    return WebApi.Get(`channels`, {});
}
export default {
    CreateChannel,
    UpdateChannel,
    GetChannel,
    GetChannelBySlug,
    GetAllChannels
};



