import WebApi from "api/WebApi";
export function CreateTopic(payload) {
    return WebApi.Post("topics", payload);
}
export function UpdateTopic(payload) {
    return WebApi.Put(`topics/${payload.id}`, payload);
}
export function FetchTopics(payload) {
    return WebApi.Get("topics", payload);
}
export default {
    CreateTopic,
    UpdateTopic,
    FetchTopics
};



